<template>
  <div>
    <!-- hero Section Starts -->
    <section id="hero">
      <div class="py-60 xl:px-96 text-center">
        <h2 class="font-heading text-5xl sm:text-4xl lg:text-6xl font-thin">{{ blogInfo.title }}</h2>
        <p class="text-xl xl:px-40 pt-5 text-stone-700">{{ blogInfo.date }}</p>

      </div>
    </section>


    <section class="jarallax">
      <img :src="getImageSrc(blogInfo.cover)" alt="" class="jarallax-img title-img">
    </section>

    <!-- content Section Starts -->
    <section id="blog-single-content">
      <div class="container mx-auto px-5 lg:px-0 banner-content">
        <div v-html="blogInfo.content" class="my-10 ">

        </div>
      </div>
    </section>

  </div>
</template>

<script>
import {blogData} from "../data/blog";

export default {
  name: "Detail",
  data() {
    return {
      blogInfo: null,
      id: 1,
    }
  },
  activated() {
    this.getInfo();
  },
  methods: {
    getImageSrc(coverPath) {
      const images = require.context('@/images', false, /\.png$/);
      return images(`./${coverPath.split('/').pop()}`);
    }, getInfo() {
      this.id = this.$route.query.id;
      this.blogInfo = blogData()[blogData().length - this.id];
      let split = this.blogInfo.content.split('\n');
      for (let i = 0; i < split.length; i++) {
        if (i % 2 === 0) {
          split[i] = '<p>' + split[i] + '</p>';
        } else {
          split[i] = '<p class="py-4">' + split[i] + '</p>';
        }
      }
      this.blogInfo.content = split.join('');
    }
  }
}
</script>

<style scoped lang="scss">


</style>



